.SideDrawer {
    position: fixed;
    width: 280px;
    max-width: 70%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 200;
    background-color: white;
    padding: 32px 16px;
    box-sizing: border-box;
    transition: transform 0.5s ease-out;
    text-align: center;
}

.Navbar {
    margin-top: 50%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
}



@media (min-width: 768px) {
    .SideDrawer {
        display: none;
    }
}

.Open {
    transform: translateX(0);
}

.Close {
    transform: translateX(-100%);
}
